.header{
  display: flex;
  width: 100%;
  align-items: flex-start;
}
.company-logo{
  width:20%;
  text-align: center;
  & img{
    width: 100px;
  }
}

.menu {
  width: 80%;
  display: flex;
  background: $white;
  font-family: sans-serif;
  margin-bottom: 2em;

  &-items {
    flex: 1 1 20%;
    padding: .5em;
    color: $black;
    text-align: center;
    text-decoration: none;

    & i{
      font-size: 1.3em;
      margin-bottom: .3em;
    }

    &-text {
      display: block;
      font-size: .6em;
    }

    &:hover{
      transition: all .3s ease-in-out;
      background: $blue;
      color: $white;
    }
  }

  .active{
    background: $blue;
    color: $white;
  }
}
