.account{
  width: 90%;
  max-width: 600px;
  margin: auto;
  background: $white;
  padding: 1em;
  color: $gray-dark;

  &-name{
    font-weight: normal;
    padding: .2em;
    text-align: center;
    margin-bottom: 1em;
    border-bottom: thin solid $gray;

  }

  .user{
    display: flex;
    align-items: center;
  & img{
    width: 100px;
    margin-right: 2em;
  }
    .user-info{
    & p{
    margin-bottom: 1em}
    }
  }

  .logout{
    display: block;
    text-align: center;
    background: $blue;
    padding: 1em;
    color: $white;
    border: none;
    margin: auto;
    width: 100%;
    max-width: 200px;
  }
}