*, html, :before, :after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


#root {
  width: 100%;
  font-size: 16px;
  background: $gray-light;
  padding: 2em;
  min-height: 100vh;
  height: 100vh;
  font-family: sans-serif;
}

.app {
  width: 100%;
  background: $gray;
  padding: 2em;
  height: 100%;

  &-header {
    width: 100%;
  }

  &-content {
    width: 100%;
  }
}