.item {
  width: 95%;
  background: $white;
  border-radius: 40px;
  color: $gray-text;
  display: flex;
  padding: 1em 2em;

  &-hour {
    text-align: center;
    border-right: thin solid $gray-text;
    width: 15%;
    padding: 1em 2em;
  }

  &-location {
    border-right: thin solid $gray-text;
    padding: 0 2em;
    display: flex;
    width: 30%;
    &-icon{
      font-size: 1.2em;
      line-height: 4;
      margin-right:1em;
    }
    &-info{
      text-align: center;
      & i{
        font-size: 2em;
        color: $orange-light;
        margin: .2em;
      }
    }
  }

  &-payment {
    color: $gray-text-dark;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    width: 55%;

    & i:first-child {
      font-size: 3.2em;
      transform: rotateX(180deg);
      transform: rotateY(180deg);
    }

    & i:last-child{
      font-size: 1.2em;
    }

    & .price {
      font-size: 2.2em;
    }

    & .quantity {
      display: inline-block;
      font-size: .5em;
      color: $white;
      background: $red;
      padding: 2em;
      border-radius: 5px;
    }
  }
}