.item-list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 1.5em;

  & > i {
    width: 50px;
    height: 50px;
    background: $orange;
    color: $white;
    text-align: center;
    line-height: 1.7;
    border-radius: 50%;
    font-size: 2em;
    margin-right: 1em;
  }

}