.aside{
  margin-top: 1em;
  margin-right: 2em;
  padding: 2em;
  display: flex;
  flex-direction: column;
  position: relative;

  &:after{
    content: '';
    display: block;
    width: 1px;
    height: 80%;
    background: $gray-dark;
    position: absolute;
    right: 0;
    top: 10%;

  }

  & .title{
    color: $gray-text-dark;
    font-weight: normal;
    text-align: center;
    margin-bottom: 1em;
    font-size: 1em;
  }

  .aside-filter{
    &:after,
  &:before{
    content: '';
    display: block;
    background: $gray-dark;
    width: 80%;
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 1.5em;
    height: 1px;
  }
  }
}

