.btn-flag {
  border: none;
  width: 80px;
  height: 80px;
  font-size: 3em;
  text-align: center;
  line-height: 1;
  position: absolute;
  bottom: 1em;
  right: 1em;
  z-index: 10;
  background: $orange;
  color: $white;
  border-radius: 50%;
}

.input-group {
  border: thin solid $gray-dark;
  background: $white;
  padding: .7em;
  color: $gray-light;
  text-align: left;
  width: 100%;
  max-width: 300px;
  border-radius: 5px;
  margin-bottom: 1em;

  & > i{
    margin-right: .7em;
  }

  & input {
    border: none;
    color: $gray-dark;
    outline: none;
    width: calc(100% - 50px);
  }
}

.btn-default{
  border: none;
  display: inline-block;
  width: 100%;
  color: $black;
  background: $white;
  font-size: 1.2em;
  padding: .5em 1em;
  text-align: center;
  border-bottom: thin $gray-dark solid;
}

.mb-1-5{
  margin: 1.5em;
}

$height : 40px;
$width: 70px;
.switch {
  position: relative;
  display: inline-block;
  width: $width;
  height: $height;

  input[type="checkbox"]{
    display:none;
  }
  input:checked + .knob {
    animation: colorChange 0.4s linear forwards;

  }
  input:checked + .knob:before {
    animation: turnON 0.4s linear forwards;

  }

}

.knob{
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: $blue;
  border-radius: $height;
}

.knob:before {
  position:absolute;
  background-color: white;
  content:"";
  left: $height*0.10;
  top: $height*0.10;
  width: ($height*0.80);
  height: ($height*0.80);
  border-radius: 50%;
}

@keyframes colorChange {
  from { background-color: $blue;}
  50%{ background-color: $orange-light;}
  to {background-color: $orange;}
}
@keyframes turnON {
  from { transform: translateX(0px);  }
  to {  transform: translateX($width - ($height*0.99) );}
}

.toggle{
  display: flex;
  justify-content: space-between;
  margin-bottom: 1.5em;
  & i{
    font-size: 2.5em;
    color: $gray-text-dark;
  }
}

.not-available{
  width: 80%;
  background: $red;
  color: $white;
  margin: auto;
  padding: 2em;
}